.dialogs {
    display: grid;
    grid-template-columns: 2fr 10fr;
    font-weight: 500;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.dialogsItem {
    width: 200px;
    margin-right: 10px
}

.dialog {
    padding: 10px 5px;
    cursor: pointer;
    display: flex;
    align-items: center;
}

.dialogsItem .active {
    color: #0404cb;
}

.userAvatar{
    margin-right: 5px;
    border: 2px solid grey;
    width: 30px;
    height: 30px;
}

.messages {
    width: 300px;
    margin-top: 10px;
}

img{
    position: relative;
    top: 10px;
}



